import React from 'react';
import { Button, Container, Row, Col, Card } from 'react-materialize';
import GoogleLogo from "../media/review/Google.svg"
import FbLogo from "../media/review/fb.png"
import logo from "../media/img/logo.png"
import { Link, useHistory } from "react-router-dom";

import * as emailjs from 'emailjs-com'
function CardContainer(props) {
  return <Container><Row><Col style={{ marginTop: "30%" }} s={12} m={6} l={4} offset="m3 l4"><Card className="blue lighten-4">{props.content}</Card></Col></Row></Container>;
}
export function ReviewForm() {
  let history = useHistory();
  function sendEmail(e) {
    e.preventDefault();
    emailjs.sendForm('service_a2kpjid', 'template_b4rmr1q', e.target, 'user_FNtbnhvNY6tLZSICaVHsY')
      .then((result) => {
        console.log(result.text);
        history.push('/ReviewThanks')  // redirect
      }, (error) => {
        console.log(error.text);
      });
  }
  return (
    <CardContainer content={<form className="contact-form" onSubmit={sendEmail}>
         <Row>
           <Col s={8} offset="s3">

            <img
            
              width="60%"
              alt="Utah Countertops"
              src={logo}
              
            />
          </Col>
          <Col s={12}>
            <h5 className="">Let us know how we did</h5>
            <input type="hidden" name="contact_number" />
            <label>Name</label>
            <input type="text" name="user_name" />
            <label>Email</label>
            <input type="email" name="user_email" />
            <label>Message</label>
            <textarea className="materialize-textarea" name="message" />
            <input className="btn green darken-3 right" type="submit" value="Send" />
            </Col>
      </Row>
    </form>} />
  );
}
export function ReviewGoogle() {
  return (
    <CardContainer content={
      <span>
        <h3 className="center-align">
          <img
            alt=""
            src={GoogleLogo}
            width="30%"
          />
        </h3>
        <h3 className="center-align">
          Do you have a Google Account?</h3>
        <h3 className="center-align">
          <Button className="center-align btn green darken-3" onClick={() => window.open("https://search.google.com/local/writereview?placeid=ChIJdapDla-JUocRm2b-doxJG3M", "_blank")} >Yes</Button></h3>
        <p className="center-align"><Link className="center-align" to="/ReviewFacebook">No</Link></p>
        <br />
      </span>
    } />
  );
}
export function ReviewThanks() {
  return (
    <CardContainer content={
      <span>
        <h3 className="center-align">
          <img
            alt=""
            src={logo}
            width="30%"
          />
        </h3>
        <h3 >
          Thank you for your input
      </h3>
        <Link className="btn btn-large center-align btn green darken-3" to="/">Return Home</Link>
      </span>} />
  );
}

export function ReviewFacebook() {
  return (
    <CardContainer content={
      <span>
        <h3 className="center-align">
          <img
            alt=""
            src={FbLogo}
            width="30%"
          />
        </h3>
        <h3 className="center-align">
          Do you have a facebook Account?</h3>
        <h3 className="center-align">
          <Button className="center-align btn green darken-3" onClick={() => window.open("https://www.facebook.com/Utah-Countertops-103067048537869/reviews/", "_blank")} >Yes</Button></h3>
        <p className="center-align"><Link className="center-align" to="/ReviewForm">No</Link></p>
        <br />
      </span>} />
  );
}

export default function Index() {
  return (
    <CardContainer content={<span><h3 className="center-align">
      <img
        alt=""
        src={logo}
        width="30%"
      />
    </h3>
      <h5 className="center-align">Would you recommend Utah Countertops?</h5>
      <h3 className="center-align">
        <Link className="btn green darken-3" to="/ReviewGoogle">Yes</Link>
      </h3>
      <p className="center-align"><Link className="center-align" to="/ReviewForm">No</Link></p>
      <br /></span>} />


  );
}



